* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Heebo", "Arial", sans-serif;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.body {
  margin: 0;
  padding: 0;
  background-color: #fff;
  color: #000;
}
.body--scrolloff {
  overflow: hidden;
}

@font-face {
  src: url("../fonts/Heebo-Bold.woff2") format("woff2"), url("../fonts/Heebo-Bold.woff") format("woff");
  font-weight: 700;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-SemiBold.woff2") format("woff2"), url("../fonts/Heebo-SemiBold.woff") format("woff");
  font-weight: 600;
  font-family: "Heebo";
  font-display: swap;
}
@font-face {
  src: url("../fonts/Heebo-Medium.woff2") format("woff2"), url("../fonts/Heebo-Medium.woff") format("woff");
  font-weight: 500;
  font-family: "Heebo";
