* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

	font-family: 'Heebo', 'Arial', sans-serif;
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.body {
	margin: 0;
	padding: 0;
  background-color: #fff;
  color: #000;

	&--scrolloff {
		overflow: hidden;
	}
}
